.cards {
  padding: 4rem;
}

.cards h1 {
  text-align: center;
  color: #f1f1f1;
  font-size: 4rem;
  background: rgb(62, 255, 62);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

video {
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: -1;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.cards__container {
  display: flex;
  flex-flow: column;
  align-items: center;
  max-width: 1520px;
  width: 95%;
  margin: 0 auto;
}

.cards__wrapper {
  position: relative;
  margin: 20px -60px 20px -90px;
  margin-left: -60px;
}

.cards__item {
  display: flex;
  flex: 1 1;
  margin: 1rem 1rem 2rem 0;
  border-radius: 15px;
}

.cards__item__link {
  display: flex;
  flex-flow: column;
  width: 100%;
  box-shadow: 0 6px 20px rgba(253, 253, 253, 0.664);
  -webkit-filter: drop-shadow(0 6px 20px rgba(179, 190, 209, 0.017));
  filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  border-radius: 10px;
  overflow: hidden;
  text-decoration: none;
}

.cards__item__pic-wrap {
  position: relative;
  width: 100%;
  padding-top: 67%;
  overflow: hidden;
}

.fade-img {
  -webkit-animation-name: fade-img;
          animation-name: fade-img;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
}

.cards__item__pic-wrap::after {
  content: attr(data-category);
  position: absolute;
  bottom: 0;
  margin-left: 10px;
  padding: 6px 8px;
  max-width: calc((100%) - 60px);
  font-size: 12px;
  font-weight: 700;
  color: #fff;
  background-color: #1f98f4;
  box-sizing: border-box;
}

.cards__item__photo {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  object-fit: cover;
  transition: all 0.2s linear;
}

.cards__item__img:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.cards__item__info {
  padding: 20px 30px 30px;
  color: aliceblue;
}

.cards__item__text {
  color: #f1ecec;
  font-size: 18px;
  line-height: 24px;
}

@media only screen and (min-width: 1200px) {
  .content__blog__container {
    width: 84%;
  }
}

@media only screen and (min-width: 1024px) {
  .cards__items {
    display: flex;
    margin-bottom: 2rem;
  }
  .cards__wrapper {
    position: relative;
    margin: 20px -60px 20px -90px;
    margin-left: -60px;
  }
}

@media only screen and (max-width: 900px) {
  .cards__wrapper {
    position: relative;
    margin: 20px -150px 10px -80px;
    margin-right: -75px;
  }
  .cards h1 {
    text-align: center;
    color: #f1f1f1;
    font-size: 3rem;
    background: linear-gradient(to top, rgb(62, 255, 62) 100%, #030303d2 50%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
}

